        v-bind="{
          outlined: value.why_jonder !== $t('whatBringsYouOffer'),
        }"
        height="55"
      >
        {{ $t("whatBringsYouOffer") }}
      </v-btn>

      <v-btn
        block
        color="#0253b3"
        class="mb-4"
